"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/app/components/ui/card";
import { Button } from "@/app/components/ui/button";
import { Calendar, Plus } from "lucide-react";
import type { DayDto } from "../types";
import ActivityList from "./ActivityList";
import CreateActivityDialog from "./CreateActivityDialog";

interface Props {
    day: DayDto;
}

export default function DayCard({ day }: Props) {
    const [openCreate, setOpenCreate] = useState(false);

    return (
        <Card>
        <CardHeader className="flex items-center justify-between">
            <div>
            <CardTitle className="flex items-center gap-3">
                <Calendar className="w-5 h-5 text-primary" />
                <span>{new Date(day.date).toLocaleDateString("pl-PL", { weekday: "short", day: "numeric", month: "short", year: "numeric" })}</span>
            </CardTitle>
            </div>
            <div>
            <Button variant="outline" onClick={() => setOpenCreate(true)} className="gap-2">
                <Plus className="w-4 h-4" /> Dodaj aktywność
            </Button>
            </div>
        </CardHeader>

        <CardContent>
            <ActivityList activities={day.activities} dayId={day.id} />
        </CardContent>

        <CreateActivityDialog open={openCreate} onOpenChange={setOpenCreate} dayId={day.id} />
        </Card>
    );
}