"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader } from "@/app/components/ui/card";
import { Button } from "@/app/components/ui/button";
import { Edit2, Trash2, MapPin, Clock } from "lucide-react";
import type { ActivityDto } from "../types";
import EditActivityDialog from "./EditActivityDialog";
import * as api from "@/lib/api";

interface Props {
  activity: ActivityDto;
}

export default function ActivityCard({ activity }: Props) {
    const [editOpen, setEditOpen] = useState(false);
    const [deleting, setDeleting] = useState(false);

    const handleDelete = async () => {
        if (!confirm("Usunąć aktywność?")) return;
        try {
        setDeleting(true);
        await api.deleteActivity(activity.id);
        // naive refresh: reload page - in app you'd call provided onRefresh or use SWR
        location.reload();
        } catch (err: any) {
        console.error(err);
        alert(err.message || "Błąd usuwania aktywności");
        } finally {
        setDeleting(false);
        }
    };

    return (
        <>
        <Card>
            <CardHeader className="flex items-start justify-between">
            <div>
                <div className="font-semibold">{activity.title}</div>
                {activity.description && <div className="text-sm text-muted-foreground">{activity.description}</div>}
                <div className="text-xs text-muted-foreground flex items-center gap-3 mt-2">
                {activity.startTime && <span className="flex items-center gap-1"><Clock className="w-3 h-3" /> {new Date(activity.startTime).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}</span>}
                {activity.location && <span className="flex items-center gap-1"><MapPin className="w-3 h-3" /> {activity.location}</span>}
                </div>
            </div>

            <div className="flex flex-col items-end gap-2">
                <div className="text-xs text-muted-foreground">{activity.createdByName ?? activity.createdBy}</div>
                <div className="flex gap-2">
                <Button variant="ghost" size="sm" onClick={() => setEditOpen(true)} className="gap-2">
                    <Edit2 className="w-4 h-4" /> Edytuj
                </Button>
                <Button variant="ghost" size="sm" onClick={handleDelete} disabled={deleting} className="gap-2">
                    <Trash2 className="w-4 h-4" /> Usuń
                </Button>
                </div>
            </div>
            </CardHeader>
        </Card>

        <EditActivityDialog open={editOpen} onOpenChange={setEditOpen} activity={activity} />
        </>
    );
}