"use client";

import type { ActivityDto } from "../types";
import ActivityCard from "./ActivityCard";

interface Props {
    activities: ActivityDto[];
    dayId: string;
}

export default function ActivityList({ activities, dayId }: Props) {
    if (!activities || activities.length === 0) {
        return <div className="text-muted-foreground">Brak aktywności w tym dniu.</div>;
    }

    // sort by startTime asc (if provided)
    const sorted = [...activities].sort((a, b) => {
        if (a.startTime && b.startTime) return a.startTime.localeCompare(b.startTime);
        if (a.startTime) return -1;
        if (b.startTime) return 1;
        return 0;
    });

    return (
        <div className="space-y-3">
        {sorted.map(act => <ActivityCard key={act.id} activity={act} />)}
        </div>
    );
}